html,
body {
  height: 100%;
}
h1,
h2,
h3 {
  font-family: "Young Serif", serif;
}

h4,
p,
a {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 22px;
}
.title-text {
  padding: 20px 0;
}
body {
  background: #ffffff;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
section {
  flex: 1 1 auto;
}

@media screen and (max-width: 576px) {
  h4,
  p,
  a,
  small,
  .lead {
    font-size: 12px;
  }
  h1 {
    font-size: 22px;
  }
  .title-text {
    padding: 0;
  }
}

/* Navbar */

.navbar-nav a {
  font-size: 22px;
  font-weight: 400;
}

@media screen and (max-width: 576px) {
  .navbar-nav a {
    font-size: 15px;
  }
  footer .navbar-nav a {
    font-size: 12px;
  }
}

.nav-btn {
  color: #f5f5f5;
  background: linear-gradient(
    230deg,
    rgba(246, 96, 231, 1) 13%,
    rgba(245, 130, 164, 1) 39%,
    rgba(244, 150, 126, 1) 64%,
    rgba(243, 182, 64, 1) 100%
  );
  box-shadow: 6px 6px 11px #e3e3e3, -6px -6px 11px #ffffff;
  border-radius: 20px;
}
.nav-btn:active {
  border: none;
  box-shadow: none;
}
.nav-item {
  padding: 0 15px;
}


/* Auth */

.flex-auth {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.auth {
  width: 451px;
  border-radius: 34px;
  display: flex;
  align-items: center;
}
.auth h1 {
  margin-bottom: 30px;
  margin-top: 40px;
}

.auth .form-control {
  margin-bottom: 20px;
  padding: 10px 28px;
  width: 335px;
}
@media screen and (max-width: 576px) {
  .auth .form-control {
    margin-bottom: 0;
  }
}

.mb-3 {
  margin-bottom: 0;
}
.form-control:focus {
  box-shadow: none;
}
.auth .form-group {
  width: 21rem;
}

.auth .form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.auth-btn {
  width: 335px;
  background-color: rgba(245, 130, 164, 1);
  color: #ffffff;
  margin-bottom: 20px;
}

.auth p {
  margin-bottom: 40px;
}
.wrap-auth {
  background: linear-gradient(
    0deg,
    rgba(246, 96, 231, 0.8) 0%,
    rgba(245, 131, 145, 0.6) 10%,
    rgba(243, 182, 64, 0.4) 24%,
    rgba(249, 221, 167, 0.2) 47%,
    rgba(254, 254, 254, 0) 72%
  );
}

/* Form validation */

.form .is-invalid input,
.form .is-invalid textarea,
.form input:invalid {
  border: 1px solid red;
}
.invalid-feedback {
  display: none;
  margin-top: 0;

  color: red;
  font-weight: bold;
}
.is-invalid .invalid-feedback {
  display: block;
}
.form input {
  outline: none;
}

/* Landing */

.landing {
  display: flex;
  flex-direction: column;
}
.px-3 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 42em;
}
.group-img {
  display: flex;
}
.group-img > * {
  width: 100%;
}
.group-img img {
  max-width: 190px;
}

.color-block {
  background: rgb(246, 96, 231);
  background: linear-gradient(
    230deg,
    rgba(246, 96, 231, 1) 13%,
    rgba(245, 130, 164, 1) 39%,
    rgba(244, 150, 126, 1) 64%,
    rgba(243, 182, 64, 1) 100%
  );
  border-radius: 30px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 80px;
}
.color-block p {
  align-content: center;
}

.btn-outline-success {
  border: none;
  background-color: #8540f5;
  color: white;
}

.flex-section {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 80px 0;
}
@media screen and (max-width: 576px) {
  .flex-section {
    padding: 10px 0;
  }
  .color-block {
    padding: 20px 20px;
  }
  .flex-section img {
    display: block;
    width: 0;
  }
}

.flex-input > * {
  padding: 10px 0;
  width: 500px;
}
.btn-outline-success {
  margin: 10px 0;
}
.buttons {
  padding: 20px 0;
}

.wrap {
  background: radial-gradient(
    40em at right,
    rgba(246, 96, 231, 1) 0%,
    rgba(245, 131, 145, 1) 15%,
    rgba(249, 221, 167, 1) 49%,
    rgba(254, 254, 254, 1) 81%
  );
  background-repeat: no-repeat;
}

@media screen and (max-width: 576px) {
  .wrap {
    background: none;
  }
}
/* Dashbord */

.dashbord-flex {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.dashbord-flex img {
  width: 150px;
  border-radius: 50px;
}

.dashbord-flex h4 {
  align-self: start;
}

.dash-buttons .light-btn:first-child {
  margin-left: 0px;
}


.light-btn {
  border-radius: 10px;
  background: #ffffff;
  margin-left: 10px;
  box-shadow: 6px 6px 11px #e3e3e3;
}
.light-btn:focus {
  color: #ffffff;
  border: none;
  background: linear-gradient(
    230deg,
    rgba(246, 96, 231, 1) 13%,
    rgba(245, 130, 164, 1) 39%,
    rgba(244, 150, 126, 1) 64%,
    rgba(243, 182, 64, 1) 100%
  );
}

/* Posts */

.wrap-posts {
  background-color: white;
}

/* Users */

.all-profiles {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.profile {
  display: flex;
  flex-direction: column;
  width: 200px;
  align-items: center;
}

.img-item {
  width: 78px;
  border-radius: 50px;
}
@media screen and (max-width: 576px) {
  .flex-post div {
    padding-right: 12px;
  }
  .img-item {
    width: 70px;
  }
  .all-profiles {
    justify-content: center;
  }
}

/* Form Post */

textarea::placeholder {
  font-family: "Poppins", sans-serif;
  padding: 5 10px;
}
.form-flex textarea {
  outline: none;
  border-radius: 20px;
  background: #ffffff;
  box-shadow: 6px 6px 19px #e0e0e0, -6px -6px 19px #ffffff;
  padding: 5px 10px;
}

.form-flex textarea:focus {
  background: linear-gradient(white, white) padding-box,
    linear-gradient(
        230deg,
        rgba(243, 182, 64, 1) 22%,
        rgba(244, 146, 134, 1) 49%,
        rgba(246, 96, 231, 1) 98%
      )
      border-box;
  border-radius: 20px;
  border: 3px solid transparent;
}
.form-flex input {
  align-self: flex-end;
}

s
/* Post item */

.btn-flex:first-child {
  padding-left: 0px;
}
.btn-flex button {
  width: 70px;
}
.card {
  border: none;
  box-shadow: 6px 6px 19px #e0e0e0, -6px -6px 19px #ffffff;
}
@media screen and (max-width: 576px) {
  .card {
    background-color: transparent;
    border: none;
    box-shadow: none;
  }
}

.link-item {
  text-decoration: none;
  color: black;
}
.card-body p {
  display: flex;

  word-break: break-all;
}
.card-text small {
  font-weight: 300;
  font-size: 16px;
}

@media screen and (max-width: 576px) {
  .material-symbols-outlined {
    font-size: 16px;
  }
  .nav-btn,
  .light-btn {
    --bs-btn-padding-y: 0.25rem;
    --bs-btn-padding-x: 0.5rem;
    --bs-btn-font-size: 0.75rem;
  }
  .btn-flex button {
    width: 55px;
  }
  .card-text small {
    font-size: 12px;
  }
}
.flex-post {
  background-color: #ffffff;
  padding: 10px 15px;
  border-radius: 20px;
  border: none;
  box-shadow: 6px 6px 19px #e0e0e0, -6px -6px 19px #ffffff;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
@media screen and (max-width: 576px) {
  .flex-post {
    justify-content: flex-start;
  }
}
.flex-img {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/* Comment */

.dlt-btn {
  background-color: red;
  color: #ffffff;
  padding: 5px;
  border-radius: 10px;
}
.flex-cmnt {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media screen and (max-width: 576px) {
  .flex-cmn {
    justify-content: flex-start;
  }
}
.flex-cmnt-item {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;
}
.flex-cmnt-item p {
  margin: 0;
}

.back-btn {
  margin-bottom: 10px;
}

