/* Footer */

.me-auto {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
footer {
  background-color: rgba(244, 146, 134, 1) 9%;
}
footer .container-xxl {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer .nav-link {
  color: black;
}
footer p {
  margin: 0;
}
